.home-container {
  display: flex;
  align-items: flex-start;
  margin-top: 20px;
}

.profile-section {
  position: sticky;
  top: 20px;
  margin-right: 20px;
  flex-shrink: 0;
  width: 150px; /* Adjust sidebar width */
  text-align: center; /* Center the sidebar content */
}

.profile-picture {
  width: 100px; /* Smaller profile picture */
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 10px; /* Add spacing below the profile picture */
}

.content-section {
  flex: 1;
  margin-left: 20px;
}

.shelf {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  justify-items: center;
  margin-top: 40px;
}

.shelf-item {
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  width: 200px;
  margin: 20px;
}

.shelf-item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 4px;
}

.shelf-item p {
  margin-top: 10px;
  font-size: 1rem;
}

.App-header {
  background-color: #f0f0f0;
  padding: 20px;
  font-size: 1.5rem;
}

.App-header nav a {
  margin: 0 10px;
  text-decoration: none;
  color: blue;
}

.App-header nav a:hover {
  text-decoration: underline;
}

/* Circular Logos */
.logo {
  width: 50px;
  height: 50px;
  margin: 5px 0;
  border-radius: 50%;
  object-fit: cover;
}

.links-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

.links-container p {
  margin-top: 5px;
  text-align: center;
}

.links-container a {
  display: inline-block;
  margin-bottom: 10px;
}

/* Only increase the font size for main page and calculator text */
.main-text, .calculator-text {
  font-size: 1.2rem; /* Increase for main content */
}

